import { Link } from "react-router-dom";
import { useLanguage } from "../context/LanguageContext";
import { useTheme } from "../context/ThemeContext";
import { trackEvent } from "../analytics";
import { ROUTES } from "../routes";

export function Header() {
  const { t, language } = useLanguage();
  const { theme, toggleTheme } = useTheme();

  const handleThemeToggle = () => {
    const next = theme === "light" ? "dark" : "light";
    trackEvent({ category: "ui", action: "theme_toggle", label: next });
    toggleTheme();
  };

  return (
    <header className="site-header">
      <div className="header-inner">
        <Link to={ROUTES.home(language)} className="site-name" aria-label={t("siteName")}>
          {t("siteName")}
        </Link>
        <nav className="header-nav">
          <a
            href={ROUTES.pricing(language)}
            className="header-link"
            onClick={() => trackEvent({ category: "nav", action: "click", label: "pricing" })}
          >
            {t("pricingTitle")}
          </a>
          <a
            href={ROUTES.help(language)}
            className="header-link"
            onClick={() => trackEvent({ category: "nav", action: "click", label: "help" })}
          >
            {t("helpTitle")}
          </a>
          <button
            type="button"
            className="theme-toggle"
            onClick={handleThemeToggle}
            aria-label={theme === "light" ? t("themeDark") : t("themeLight")}
            title={theme === "light" ? t("themeDark") : t("themeLight")}
          >
            <span className="theme-icon theme-icon-sun" aria-hidden>
              
            </span>
            <span className="theme-icon theme-icon-moon" aria-hidden>
              
            </span>
          </button>
          <Link
            to="/en"
            className={`lang-btn ${language === "en" ? "active" : ""}`}
            onClick={() => trackEvent({ category: "ui", action: "language_switch", label: "en" })}
            aria-pressed={language === "en"}
            aria-label="English"
          >
            {t("langEn")}
          </Link>
          <span className="lang-sep" aria-hidden>
            |
          </span>
          <Link
            to="/"
            className={`lang-btn ${language === "ru" ? "active" : ""}`}
            onClick={() => trackEvent({ category: "ui", action: "language_switch", label: "ru" })}
            aria-pressed={language === "ru"}
            aria-label=""
          >
            {t("langRu")}
          </Link>
        </nav>
      </div>
    </header>
  );
}
