import {
  createContext,
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useState,
  type ReactNode,
} from "react";
import { useLocation, useNavigate } from "react-router-dom";
import {
  translations,
  type Language,
  type TranslationKey,
} from "../translations";
import { pathnameToLanguage, languageToPath } from "../routes";
import { SITE_BASE_URL } from "../config";

function setDocumentHead(lang: Language, baseUrl: string) {
  if (typeof document === "undefined") return;
  const t = translations[lang];
  const base = baseUrl.replace(/\/$/, "");
  const path = lang === "en" ? "/en/" : "/";
  const canonical = `${base}${path}`;

  document.documentElement.lang = lang === "ru" ? "ru" : "en";
  document.title = t.metaTitle;

  const desc = document.querySelector('meta[name="description"]');
  if (desc) desc.setAttribute("content", t.metaDescription);

  let linkCanonical = document.querySelector<HTMLLinkElement>('link[rel="canonical"]');
  if (!linkCanonical) {
    linkCanonical = document.createElement("link");
    linkCanonical.rel = "canonical";
    document.head.appendChild(linkCanonical);
  }
  linkCanonical.href = canonical;

  const setMeta = (selector: string, attr: string, value: string) => {
    const el = document.querySelector(selector);
    if (el) el.setAttribute(attr, value);
  };
  setMeta('meta[property="og:url"]', "content", canonical);
  setMeta('meta[property="og:title"]', "content", t.metaTitle);
  setMeta('meta[property="og:description"]', "content", t.metaDescription);
  setMeta('meta[property="og:locale"]', "content", lang === "ru" ? "ru_RU" : "en_US");
  setMeta('meta[name="twitter:title"]', "content", t.metaTitle);
  setMeta('meta[name="twitter:description"]', "content", t.metaDescription);

  const setHreflang = (hreflang: string, href: string) => {
    let link = document.querySelector<HTMLLinkElement>(`link[rel="alternate"][hreflang="${hreflang}"]`);
    if (!link) {
      link = document.createElement("link");
      link.rel = "alternate";
      link.setAttribute("hreflang", hreflang);
      document.head.appendChild(link);
    }
    link.href = href;
  };
  setHreflang("ru", `${base}/`);
  setHreflang("en", `${base}/en/`);
  setHreflang("x-default", `${base}/`);
}

type LanguageContextValue = {
  language: Language;
  setLanguage: (lang: Language) => void;
  t: (key: TranslationKey) => string;
};

const LanguageContext = createContext<LanguageContextValue | null>(null);

export function LanguageProvider({ children }: { children: ReactNode }) {
  const location = useLocation();
  const navigate = useNavigate();
  const languageFromPath = pathnameToLanguage(location.pathname);
  const [language, setLanguageState] = useState<Language>(languageFromPath);

  useEffect(() => {
    setLanguageState(languageFromPath);
  }, [languageFromPath]);

  useEffect(() => {
    setDocumentHead(language, SITE_BASE_URL);
  }, [language]);

  const setLanguage = useCallback(
    (lang: Language) => {
      setLanguageState(lang);
      const path = languageToPath(lang);
      if (pathnameToLanguage(location.pathname) !== lang) {
        navigate(path);
      }
    },
    [location.pathname, navigate]
  );

  const t = useCallback(
    (key: TranslationKey) => translations[language][key],
    [language]
  );

  const value = useMemo<LanguageContextValue>(
    () => ({ language, setLanguage, t }),
    [language, setLanguage, t]
  );

  return (
    <LanguageContext.Provider value={value}>{children}</LanguageContext.Provider>
  );
}

export function useLanguage() {
  const ctx = useContext(LanguageContext);
  if (!ctx) throw new Error("useLanguage must be used within LanguageProvider");
  return ctx;
}
