import type { Language } from "./translations";

/** Path for language (RU = /, EN = /en). */
export function languageToPath(lang: Language): string {
  return lang === "en" ? "/en" : "/";
}

/** Language from pathname. /en or /en/* -> en, else ru. */
export function pathnameToLanguage(pathname: string): Language {
  const normalized = pathname.replace(/\/$/, "") || "/";
  return normalized === "/en" || normalized.startsWith("/en/") ? "en" : "ru";
}

export const ROUTES = {
  home: (lang: Language) => (lang === "en" ? "/en" : "/"),
  pricing: (lang: Language) => (lang === "en" ? "/en#pricing" : "/#pricing"),
  help: (lang: Language) => (lang === "en" ? "/en#help" : "/#help"),
} as const;
