import { ReactGAImplementation } from "react-ga4";

/** Shared GA4 instance (initialized in GoogleAnalytics). */
export const reactGA = new ReactGAImplementation();

/** Send a pageview (e.g. on initial load or route change). */
export function trackPageView(path: string = window.location.pathname || "/", title?: string) {
  reactGA.send({
    hitType: "pageview",
    page: path,
    title: title ?? document.title,
  });
}

type EventParams = {
  category: string;
  action: string;
  label?: string;
  value?: number;
  nonInteraction?: boolean;
  transport?: "beacon" | "xhr" | "image";
};

/** Send a custom event. */
export function trackEvent({ category, action, label, value, nonInteraction, transport }: EventParams) {
  reactGA.event({
    category,
    action,
    ...(label != null && { label }),
    ...(value != null && { value }),
    ...(nonInteraction != null && { nonInteraction }),
    ...(transport != null && { transport }),
  });
}
