/**
 * Bot and group URLs. Override at build time with BUN_PUBLIC_* env vars.
 */
function getEnv(key: string): string | undefined {
  if (typeof process === "undefined" || !process.env) return undefined;
  return process.env[key];
}

export const VPN_BOT_URL =
  getEnv("BUN_PUBLIC_VPN_BOT_URL") ?? "https://t.me/meaw_vpn_bot?start=5QB7G3";
export const SUPPORT_BOT_URL =
  getEnv("BUN_PUBLIC_SUPPORT_BOT_URL") ?? "https://t.me/meaw_vpn_support_bot";
export const TELEGRAM_GROUP_URL =
  getEnv("BUN_PUBLIC_TELEGRAM_GROUP_URL") ?? "https://t.me/+phbIF2tj2NE3NmM5";

/**
 * Google Analytics 4 measurement ID. Required.
 * Use direct process.env access so Bun --env='BUN_PUBLIC_*' inlines it into the bundle.
 */
function getGAMeasurementId(): string {
  const id = process.env.BUN_PUBLIC_GA_MEASUREMENT_ID;
  if (!id || typeof id !== "string" || id.trim() === "") {
    throw new Error(
      "BUN_PUBLIC_GA_MEASUREMENT_ID is required. Set it when building, e.g. BUN_PUBLIC_GA_MEASUREMENT_ID=G-XXXXXXXXXX bun run build"
    );
  }
  return id.trim();
}

export const GA_MEASUREMENT_ID = getGAMeasurementId();

/**
 * Base path when the app is not at the domain root (e.g. GitHub Pages: /website).
 * Set BUN_PUBLIC_BASE_PATH at build so routes resolve under /website/.
 */
export const BASE_PATH = process.env.BUN_PUBLIC_BASE_PATH ?? "";

/** Site base URL for canonical, hreflang, OG. Include base path if set (e.g. https://meawvpn.github.io/website). */
export const SITE_BASE_URL =
  getEnv("BUN_PUBLIC_SITE_BASE_URL") ??
  (BASE_PATH ? `https://meawvpn.github.io${BASE_PATH}` : "https://meawvpn.com");
